import React, { useRef, Suspense, useState } from "react";
import Loader from "../components/Loader";
import JsonData from "../json/data.json";
import TransitionLink from "../hooks/TransitionLink";
const GlassBtn = ({ title,tagLine, data }) => {
  const dialogRef = useRef();
  const [broken, setBroken] = useState(false);
  return (
    <>
      <section
        onClick={() => (data ? dialogRef.current.showModal() : setBroken(true))}
        className={`btn_glass_purple_parent ${broken?'broken_glass':''}`}
      >
        <div className=" btn_glass_purple gradient_purple_text"><span className="">{title}</span></div>
      </section>

      <dialog
        ref={dialogRef}
        className="transition-all rounded-lg z-10 bg-white/40 p-10 backdrop-blur-2xl"
      >
        <form method="dialog" className="absolute top-0 right-0 mr-4">
          <button className="btn_glass_purple_parent p-4">
            <span className="btn_glass_purple gradient_purple_text">X</span>
          </button>
        </form>
        <p className="text-2xl Anuphan">{tagLine}</p>
        <ul className="flex flex-col my-5">
          {data &&
            data.map(({ title, route }, index) => (
              <li className="btn_glass_purple_parent " key={index}>
                <TransitionLink to={route} className='btn_glass_purple gradient_purple_text'>{title}</TransitionLink>
              </li>
            ))}
        </ul>
      </dialog>
    </>
  );
};
const Collection = () => {
  return (
    <Suspense fallback={<Loader />}>
      <ul className="flex flex-wrap justify-between cursor-pointer p-4 gap-2">
        {JsonData.projects.map((project, index) => (
          <li className="flex-[45%]" key={index}>
            <GlassBtn {...project} />
          </li>
        ))}
      </ul>
    </Suspense>
  );
};

export default Collection;
